/* istanbul ignore file */

import { FOCUSABLE_ELEMENTS_SELECTOR } from '../utility/constants';
import { getHTMLElement } from '../utility/helper';

export function mouseEnter(el: Element | null): boolean {
  if (el === null) {
    return false;
  }

  return el.dispatchEvent(new MouseEvent('mouseenter', { bubbles: true }));
}

export function mouseLeave(el: Element | null): boolean {
  if (el === null) {
    return false;
  }

  return el.dispatchEvent(new MouseEvent('mouseleave', { bubbles: true }));
}

export function mouseClick(el: Element | null): boolean {
  if (el === null) {
    return false;
  }

  return el.dispatchEvent(new MouseEvent('click', { bubbles: true }));
}

export function tabKeyDown(el: Element | null, shiftKey = false): boolean {
  if (el === null) {
    return false;
  }

  return el.dispatchEvent(new KeyboardEvent('keydown', { bubbles: true, shiftKey, key: 'Tab' }));
}

export function keyDown(el: Element | null, key: string): boolean {
  if (el === null) {
    return false;
  }

  return el.dispatchEvent(new KeyboardEvent('keydown', { bubbles: true, key }));
}

export function getFirstFocusable(el: Element | null): HTMLElement | null {
  if (el === null) {
    return null;
  }

  return getHTMLElement(el.querySelector(FOCUSABLE_ELEMENTS_SELECTOR));
}

export function getFocusableByDataId(el: Element | null, elementId: string): HTMLElement | null {
  if (el === null) {
    return null;
  }

  const elements = getAllFocusables(el);
  if (elements === null) {
    return null;
  }

  return elements.find((element) => element?.getAttribute('data-id') === elementId) ?? null;
}

export function getLastFocusable(el: Element | null): HTMLElement | null {
  if (el === null) {
    return null;
  }

  const focusableElements = el.querySelectorAll(FOCUSABLE_ELEMENTS_SELECTOR);
  return getHTMLElement(focusableElements[focusableElements.length - 1]);
}

export function getAllFocusables(el: Element | null): (HTMLElement | null)[] | null {
  if (el === null) {
    return null;
  }

  const focusables = el.querySelectorAll(FOCUSABLE_ELEMENTS_SELECTOR);

  if (focusables === null) {
    return null;
  }

  return Array.from(focusables).map((element) => getHTMLElement(element));
}

export function endAnimation(el: Element | null, animationName: string): boolean {
  if (el === null) {
    return false;
  }

  // this is a hacky workaround because jest doesn't have AnimationEvent implemented
  const e = new Event('animationend', { bubbles: true });
  Object.assign(e, { animationName });

  return el.dispatchEvent(e);
}

export function startAnimation(el: Element | null, animationName: string): boolean {
  if (el === null) {
    return false;
  }

  // this is a hacky workaround because jest doesn't have AnimationEvent implemented
  const e = new Event('animationstart', { bubbles: true });
  Object.assign(e, { animationName });

  return el.dispatchEvent(e);
}

export function focus(el: Element | null): boolean {
  if (el === null) {
    return false;
  }

  return el.dispatchEvent(new Event('focus'));
}

export function blur(el: Element | null): boolean {
  if (el === null) {
    return false;
  }

  return el.dispatchEvent(new Event('blur'));
}

export function sendInput(el: HTMLInputElement | null, input: string): boolean {
  if (el === null) {
    return false;
  }

  el.value = input;
  return el.dispatchEvent(new Event('input'));
}

export function submit(el: Element | null): boolean {
  if (el === null) {
    return false;
  }

  return el.dispatchEvent(new Event('submit', { bubbles: true }));
}
